@import '/src/styles/define.scss';



.main{
  display: flex;
  flex-direction: column;
	height: inherit;
	// background-color: #aaa;
  padding-top: 1em;

  
  .title{
    @media (max-width: $breakpoint-desktop) { display: none;  }
    flex: 1;
    // background-color: #393;
    width: 100%;

    .buttonGroup {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // font-family: 'kdg_Medium', sans-serif; // Or similar brush font
      gap: 1em;
      padding: 0em 1.5em;

      
      .customButton {
        line-height: 2em;
        background: none;
        width: 100%;
        font-size: 1.5em;
        font-weight: bold;
        color: #666; // Darker color on hover
        cursor: pointer;
        outline: none;
        white-space: nowrap;

        border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
        border-radius: 25px;
      }
      
      .selected{
        border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
        background-color: rgba(145, 136, 57, 0.5);
        outline: none;
        font-size: 1.5em; // Adjust size as needed
        color: #000; // Darker color on hover
        font-weight: bold;
      }
    
      .separator {
        color: #000; // Gray color for separator
        padding-left: 10px;
        padding-right: 10px;
        font-size: 1.8em; // Match button size
      }
    }
    
  }

  .content{
    flex: 8;
    width: 100%;
    
  }

}
